body {

}

.App {
  font-size: 14px;
  line-height: 1.42857143;

  max-width: 1000px;
  margin: auto;
  padding-left: 5px;
  padding-right: 5px;
  border: 1px solid red;

  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif
;
}

table {
  td th {
    border: 2px solid;
  }
}

.relatedSectionOfNumbers {
  border: 1px solid #ccc;
  padding: 5px;
  margin-left: 5px;
  margin-right: 5px;
  margin-bottom: 5px;
  border-radius: 4px;
  width: 400px;

  -webkit-box-shadow: 0 1px 1px rgba(0,0,0,.075);

          box-shadow: 0 1px 1px rgba(0,0,0,.075);

}

td {
  padding-right: 4px;
  border: 4px;
}

.column-heading {
  text-align: right;
  font-weight: bold;
  /*font-size: 13px;*/
}

.column-data {
  text-align: left;
  /*font-size: 10px;*/
  white-space: nowrap;
}

.minor-column-heading {
  text-align: right;
  color: #555;
  font-weight: 400;
  /*font-size: 10px;*/
}

.minor-data {
  color: #555;
  font-weight: 200;
  /*font-style: italic;*/
  /*font-size: 9px;*/
}

input, .column-data, .minor-data, .money-input, .money-display {
  color: #555;
  padding: 6px 6px;
  font-size: 14px;
  width: 80px;
  text-align: left;
}

input {
  border: 1px solid #ccc;
  border-radius: 4px;
  -webkit-box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
          box-shadow: inset 0 1px 1px rgba(0,0,0,.075);
}

.percentageInput {
  width: 60px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.sideBySideRow {
  border: 1px solid red;
  clear: both;
  overflow: auto;
}

.sideBySideColumn {
  display: inline-block;
}

.input-group, .display-group {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: center;
      align-items: center;
  margin-bottom: 5px;
}
.input-subgroup {
  display: inline;
}

.input-label, .display-label {
  text-align: right;
  font-weight: bold;
  margin-right: 10px;
}

.input-unit, .display-unit {
  margin-right: 3px;
}





